import firebase from "firebase";

var docRef = firebase.database().ref();

function global() {
  let global;

  docRef
    .child("lista")
    .get()
    .then(async (snapshot) => {
      await (global = snapshot.val());
    });

  setTimeout(() => {
    const horario = () => { };
    horario();
  }, 3000);
  return global
}

